.page-header {
    background-color: $yellow;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 10;

    &__inner {
        @include container;
        border-top: none;
        display: flex;
        justify-content: space-between;

    }

    &__logo {
        display: block;
        font-size: 0px;
        width: 100px;
        height: 80px;
        padding-top: 10px;
        padding-left: 10px;

        img {
            max-width: 100%;
            height: auto;
        }
    }

    // &__nav-toggler-wrapper {
    //     position: relative;
    //     font-family: sans-serif;
    // }
}

@media(min-width: 1024px) {
    .page-header {
        &__logo {
                width: 140px;
        }
    }
}

@media(min-width: 1200px) {
    .page-header {
        &__inner {
                width: 1170px;
        }
    }
}
